* {
  font-family: 'Roboto', sans-serif;

}

h2, h4 {
  font-family: 'Space Grotesk', sans-serif;
}

.App {
  background-color: #828282;
}

.header {
  width: 100%;
  padding: 2%;
  background-color: burlywood;
  color: white;
  text-align: center;
}

.text-center {
  width:100%;
}

.display-board {
  width: 100%;
  background-color: #F8F8F8;
  padding: 20px;
  border-radius: 10px 10px 10px 10px;
}

.current-user {
  background-color: lightblue;
  padding: 5%;
  position:relative;

  left: 365px;
  margin-top: 80px;
}

.current-user.active {
  left: 0px;
  margin-top: 0px;
}

.new-user {
  background-color: lightyellow;
  padding-bottom: 10px;
}

.number {
  color: black;
  font-size: 40px;
  font-weight: bold;
  text-align: center;
}

.mrgnbtm {
  margin-top: 20px;
}

.priceBoxes {
  background-color: white;    /* rgb(214,51,132, 0.1); PINK */
  margin-bottom: 10px;
}

.priceBoxes .col {
  background-color: rgba(112,44,249,.1);    /* rgba(255,228,132,1); */
  border: 3px solid rgba(112,44,249,0.25);
  border-width: 2px 2px 2px 3px;
}

.priceBoxes .col.top {
  border-radius: 0;
}

.tsForm, .trForm {
  border: 2px solid black;
  border-radius: 5px;
  padding: 10px;
}

.ssForm {
  background-color: #F8F8F8;
}

.swapTitle {
  margin-top: 20px;
  margin-bottom: 15px;
}

.swapTitle span {
  font-weight: 700;
  text-decoration: underline;
}

.swapBox {
  margin: 5px;
  border-radius: 6px;
  padding: 20px;
  box-shadow: 0px 0px 8px rgba(0,0,0,0.18);
}

.muted {
  color: #313131;
  font-weight: 300;
  margin-top: 1rem;
}

#calcAmt {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 20px;
}

.tsForm h5, .trForm h5 {
  font-weight: 700;
  font-style: italic;
  border-bottom: 2px solid blue;
}

#selectBS, #sel-cur, #tradeAmount, #currencyOut, #currencyIn {
  width: 90%;
}

#tradePrice {
  width: 70%;
}

#tradeSubmit {
  float: right;
}

.qrContainer {
  border: 2px solid black;
  border-radius: 5px;
  padding: 5px;
  margin: 10px auto;
  text-align: center;
}

.qrContainer img {
  background: white;
  padding: 5px;
  border-radius: 5px;
}

/* real design style starts here */
.profileContainer {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  overflow:hidden;
  text-align:center;
}

.profileContainer .userLogo {
  display: inline;
  margin: -6px -8px;
  height: 115%;
  width: auto;
}

.alertContainer {
  float:right;
  width:50px;
  height:50px;
  border-radius:6px;
  overflow:hidden;
  text-align:center;

  box-shadow: 0px 0px 8px rgba(0,0,0,0.18);
}

.alertContainer .alertLogo {
  display: inline;
  margin: 0 auto;
  height: 100%;
  width: auto;
}

.balanceLabel {
  text-align:center;
}

.hl {
  color: red;
}

.buttonContainer {
  text-align: center;
  width: 100%;
}

.bigBtn {
  display:inline-block;
  padding: 15px 33%;
}

.tokenBadge {
  display: inline;
  margin: 0 auto;
  height: 100%;
  width: auto;
}

.badgeContainer {
  width: 65px;
  height: 65px;
  border-radius: 50%;
  overflow:hidden;
  text-align:center;
}

.tokenLabel span {
  color: red;
}

.table tr.surplus {
  display:none;
}

.table[isactive="true"] tr.surplus {
  display: block;
}

.tokenBox {
  margin: 5px;
  border-radius: 6px;
  padding: 5px;
  box-shadow: 0px 0px 8px rgba(0,0,0,0.18);
}

.flipButtonBox {
  width: 40px;
  height: 40px;
  margin-top: 15px;
  margin-bottom: 15px;
  margin-left:40%;
  border-radius: 30%;
  overflow:hidden;
  text-align:center;
  box-shadow: 0px 0px 8px rgba(0,0,0,0.18);
}

.flipButtonBox img {
  margin: 12px;
}

.miniPrices {
  list-style: none;
  margin:0;
  padding:0;
}

.miniPrices li {
  float:left;
  margin-right: 5px;
}

.maxBtn {
  float: right;
  padding: 5px;
  margin: 5px 10px;
  cursor: pointer;
}

#swapSubmit {
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
}
